.nav {
    width: 100%;
    position: relative;
    margin-top: 10px;
    margin-bottom: 40px;
    max-width: 1000px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    .profile-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 1px solid #ccc;
        padding: 4px 6px;
        border-radius: 4px;
        background: white;
        position: absolute;
        top: 60px;
        right: -10px;
        li {
            font-size: 14px;
            margin: 5px 0;
            color: #747474;
        }
    }
    .nav-logo img {
        width: 50px;
        height: 50px;
    }
    .nav-item {
        display: flex;
        flex-direction: row;
        align-items: center;
        .create-button {
            font-weight: 700;
        }
        div {
            cursor: pointer;
            user-select: none;
            margin-left: 20px;
            font-size: 14px;
        }
        .profile-image {
            width: 40px;
            height: 40px;
            margin-left: 12px;
            border: 1px solid black;
            border-radius: 50%;
        }
    }
}
@media screen and (max-width: 768px) {
    .nav {
        width: 600px;
    }
}
@media screen and (max-width: 414px) {
    .nav {
        width: 380px;
    }
}
